import './App.css';
import './compo/css/Common.css';
import './compo/css/Main.css';
import './compo/css/Animate.css';
import { BrowserRouter, Route, Switch, Link } from "react-router-dom";
import Channel from './compo/js/Channel';
import Exer from './compo/js/Exer';
import Maraton from './compo/js/Maraton';
import Online from './compo/js/Online';
import Event from './compo/js/Event';
import Notice from './compo/js/Notice';
import Main from './compo/js/Main';

function App() {
    return (

        <BrowserRouter basename="/festival">
        <div id="container" className="main">
            <header className="header" id="header">
                <div className="headLogo clear">
                    <div className="headInner">
                        <h1 className="logo">
                            <Link to="/">
                                <img src="./img/header/logo.png" alt="로고없엉" />
                            </Link>
                        </h1>
                    </div>
                </div>
            </header>
        </div>
        <Switch>
            <Route exact path="/"> <Main /> </Route>
            <Route path="/Channel"> <Channel /> </Route>
            <Route path="/Exer"> <Exer /> </Route>
            <Route path="/Maraton"> <Maraton /> </Route>
            <Route path="/Online"> <Online /> </Route>
            <Route path="/Event"> <Event /> </Route>
            <Route path="/Notice"> <Notice /> </Route>
        </Switch>
        </BrowserRouter>
    );
}

export default App;
