import '../css/Main.css';
import '../css/Common.css';
import '../css/Exer.css';
import Jexer from "../../data/exer.json";
import {useState, useRef, useEffect } from "react";
import { BrowserRouter, Route, Switch, Link } from "react-router-dom";

export default function Exer() {

    let [data,setData] = useState(Jexer.mire);

    return (
        <>
            <div className="menu menu2" id="menu2">
                <div className="side sPopup">
                    <div className="sideCon">
                        <div className="sideHead">
                            <Link to="/" className="close">
                                close
                                <span></span>
                                <span></span>
                            </Link>
                            <div className="ment">
                                <div className="inner">                
                                    <h2 className="tit">메이커 전시체험</h2>
                                    <p className="txt">
                                        서울 학생 메이커 괴짜축제가 엄선한 메이커 체험!
                                        <span className="block">
                                            메이커 체험 키트를 신청하고 온라인을 통해서 메이커 체험을 진행해보세요!
                                        </span>
                                    </p>
                                </div>
                            </div>
                            <div className="navWrap">
                                <ul className="nav">
                                    <li onClick={ () => { setData(Jexer.mire) }} id="m2tab1">
                                        <a className={ (data == Jexer.mire) ? "on" : ""} href="#" data-tab="tab2_1">
                                            미래존
                                            <p className="sTit">(AI/VR/드론)</p>
                                        </a>
                                    </li>
                                    <li onClick={ () => { setData(Jexer.hap) }} id="m2tab2">
                                        <a className={ (data == Jexer.hap) ? "on" : ""} href="#" data-tab="tab2_1">
                                            융합존
                                            <p className="sTit">(코딩/피지컬 컴퓨팅)</p>
                                        </a>
                                    </li>
                                    <li onClick={ () => { setData(Jexer.sangsang) }} id="m2tab3">
                                        <a className={ (data == Jexer.sangsang) ? "on" : ""} href="#" data-tab="tab2_1">
                                            상상존
                                            <p className="sTit">(디지털메이킹)</p>
                                        </a>
                                    </li>
                                    <li onClick={ () => { setData(Jexer.zza) }} id="m2tab4">
                                        <a className={ (data == Jexer.zza) ? "on" : ""} href="#" data-tab="tab2_1">
                                            괴짜존
                                            <p className="sTit">(아날로그메이킹)</p>
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className="inner clear">
                            <div className="tabCont">
                                {
                                    data.map(function(dataList,k){
                                        return(
                                            <div className="sec sec1" key={k}>
                                                <div className="boxL" id="test_1_0">
                                                    <div className="youtube_thumbnail">
                                                        <a href={dataList['vdo']} target="_blank">
                                                            <img onload="youtube_check($(this))" src={dataList['img']} />
                                                        </a>
                                                    </div>
                                                </div>
                                                <div className="boxR">
                                                    <div className="titBox">
                                                        <p className="bottom">{dataList['title']}</p>
                                                    </div>
                                                    <div className="txtBox">
                                                        <p className="txt">
                                                            {dataList['text']}<br />
                                                        </p>
                                                    </div>
                                                    <div className="btnBox">
                                                        <a href="javascript:return false" className="btnL btn off">
                                                            <span className="btnWrap">{dataList['left']}</span>
                                                        </a>
                                                        <a href="javascript:return false" className={ (dataList['right'] == undefined) ? "btnR btn off hidden" : "btnR btn off"}>
                                                            <span className="btnWrap">{dataList['right']}
                                                            </span>
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>
                                        );
                                    })
                                }
                            </div>
                        </div>
                    </div>
                    <div className="bg"></div>
                </div>
            </div>
        </>
    );
};